@font-face {
    font-family: Poppins;
    src: url("assets/fonts/Poppins-Medium.ttf") format("truetype");
    font-weight: 500;
}

@font-face {
    font-family: Poppins;
    src: url("assets/fonts/Poppins-Regular.ttf") format("truetype");
    font-weight: 400;
}

@font-face {
    font-family: Poppins;
    src: url("assets/fonts/Poppins-SemiBold.ttf") format("truetype");
    font-weight: 600;
}

body {
    font-family: Poppins, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f0f0f0;
    dir: "ltr";
}

textarea:focus {
    outline: none;
}

.container {
    max-width: 480px; /* Adjust as needed for your app */
    margin: 0 auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.swiper-wrapper {
    padding-top: 5px;
    padding-bottom: 5px;
}

.mySwiper2 .swiper-wrapper {
    padding: 0;
}

.fill-svg-nav svg path {
    fill: #ffffff;
}

.moodSVg svg circle {
    r: 15px; /* Set the radius of the circle */
    width: 30px; /* Set the width of the circle */
    height: 30px; /* Set the height of the circle */
    fill: #e7703d;
    stroke: #fff;
    stroke-width: 4px;
}

.moodSVg {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 0;
    margin-left: -36px;
}

.moodSVg svg {
    height: 100px;
    width: 400px; /* Add width to maintain aspect ratio */
}

.roadMap svg {
    width: 100%;
    height: 100%;
}

.journeyScreen {
    height: calc(100vh - 321px);
}

.breathOut {
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    animation: rotating 6000ms linear infinite;
}

.breathIn {
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    animation: rotating 4000ms linear infinite;
}

.breathStop {
    position: absolute;
    top: 50%;
    left: 50%;
    translate: -50% -50%;
    animation: none;
}

.apexcharts-toolbar {
    display: none !important;
}

.apexcharts-yaxis-texts-g {
    display: none;
}
.rhap_progress-indicator{
    background: #E7703D !important;
}
.rhap_main-controls-button{
    color: #424874 !important;
}
.rhap_volume-indicator{
    background: #424874 !important ;
}
.rhap_volume-button{
    color: #424874 !important;
}
.rhap_repeat-button{
    display: none ;
}


@keyframes breathe-in {
    from {
        transform:translate(-50%,-50%) scale(1);

    }
    to {
        transform:translate(-50%,-50%)  scale(0.5);
    }
}

@keyframes hold {
    from {
        transform:translate(-50%,-50%)  scale(0.5);
    }
    to {
        transform:translate(-50%,-50%)  scale(0.5);
    }
}

@keyframes breathe-out {
    from {
        transform:translate(-50%,-50%) scale(0.5);
    }
    to {
        transform:translate(-50%,-50%)  scale(1);
    }
}
.breathe-in-circle {
    animation: breathe-in 4s infinite;
}

.breathe-stop-circle {
    animation: none;
}

.hold-circle {
    animation: hold 2s infinite;
}

.breathe-out-circle {
    animation: breathe-out 6s infinite;
}
.playSVG svg{
    width: 20px ;
    height: 20px;
}
.custom-after::after {
    content: "";
    display: block;
    width: 24px; /* Adjust the size as needed */
    height: 24px; /* Adjust the size as needed */
    background-image: url('./assets/photo/giphy-2\ 1.gif');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    margin-top: auto; /* Adjust spacing if needed */
}
@keyframes rotating {
    from {
        -ms-transform: rotate(0deg) scale(1);
        -moz-transform: rotate(0deg) scale(1);
        -webkit-transform: rotate(0deg) scale(1);
        -o-transform: rotate(0deg) scale(1);
        transform: rotate(0deg) scale(1);
    }
    to {
        -ms-transform: rotate(360deg) scale(1);
        -moz-transform: rotate(360deg) scale(1);
        -webkit-transform: rotate(360deg) scale(1);
        -o-transform: rotate(360deg) scale(1);
        transform: rotate(360deg) scale(1);
    }
}

/* Styles for mobile devices */
@media only screen and (max-width: 600px) {
    .container {
        max-width: 100%;
        border-radius: 0;
    }
}

/* Hide container on larger screens */
@media only screen and (min-width: 601px) {
    .container {
        display: none;
    }
}

@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";
