.loader1 {
    width: 24px;
    height: 24px;
    border: 3px solid #fff;
    border-bottom-color: #e7703d;
    border-radius: 30px;
    display: inline-block;
    box-sizing: border-box;
    animation: rotate 1s linear infinite;
  }
  
  .loader2 {
    width: 40px;
    position: absolute;
    left: 43%;
    top: 46%;
    transform: translate(-50%,-50%);
    aspect-ratio: 1;
    border-radius: 50%;
    background: radial-gradient(farthest-side, #E7703D 94%, transparent) top/8px 8px no-repeat,
      conic-gradient(transparent 30%,   #E7703D);
    -webkit-mask: radial-gradient(farthest-side, transparent calc(100% - 8px), #ebe7e7 0);
    animation: rotate 1s infinite linear;
  }
  
  @keyframes rotate {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  